import * as child_process from 'child_process';
import * as fs from 'fs';
import * as path from 'path';
import type { Readable } from 'stream';
import * as vscode from 'vscode';
import { TypeScriptServiceConfiguration } from '../configuration/configuration';
import { $e } from '../utils/dispose';
import { $X } from './api';
import type * as Proto from './protocol/protocol';
import { TsServerLog, TsServerProcess, TsServerProcessFactory, TsServerProcessKind } from './server';
import { $db } from './versionManager';
import { $_ } from './versionProvider';
import { $eb } from './nodeManager';


const defaultSize: number = 8192;
const contentLength: string = 'Content-Length: ';
const contentLengthSize: number = Buffer.byteLength(contentLength, 'utf8');
const blank: number = Buffer.from(' ', 'utf8')[0];
const backslashR: number = Buffer.from('\r', 'utf8')[0];
const backslashN: number = Buffer.from('\n', 'utf8')[0];

class ProtocolBuffer {

	private a: number = 0;
	private b: Buffer = Buffer.allocUnsafe(defaultSize);

	public append(data: string | Buffer): void {
		let toAppend: Buffer | null = null;
		if (Buffer.isBuffer(data)) {
			toAppend = data;
		} else {
			toAppend = Buffer.from(data, 'utf8');
		}
		if (this.b.length - this.a >= toAppend.length) {
			toAppend.copy(this.b, this.a, 0, toAppend.length);
		} else {
			const newSize = (Math.ceil((this.a + toAppend.length) / defaultSize) + 1) * defaultSize;
			if (this.a === 0) {
				this.b = Buffer.allocUnsafe(newSize);
				toAppend.copy(this.b, 0, 0, toAppend.length);
			} else {
				this.b = Buffer.concat([this.b.slice(0, this.a), toAppend], newSize);
			}
		}
		this.a += toAppend.length;
	}

	public tryReadContentLength(): number {
		let result = -1;
		let current = 0;
		// we are utf8 encoding...
		while (current < this.a && (this.b[current] === blank || this.b[current] === backslashR || this.b[current] === backslashN)) {
			current++;
		}
		if (this.a < current + contentLengthSize) {
			return result;
		}
		current += contentLengthSize;
		const start = current;
		while (current < this.a && this.b[current] !== backslashR) {
			current++;
		}
		if (current + 3 >= this.a || this.b[current + 1] !== backslashN || this.b[current + 2] !== backslashR || this.b[current + 3] !== backslashN) {
			return result;
		}
		const data = this.b.toString('utf8', start, current);
		result = parseInt(data);
		this.b = this.b.slice(current + 4);
		this.a = this.a - (current + 4);
		return result;
	}

	public tryReadContent(length: number): string | null {
		if (this.a < length) {
			return null;
		}
		const result = this.b.toString('utf8', 0, length);
		let sourceStart = length;
		while (sourceStart < this.a && (this.b[sourceStart] === backslashR || this.b[sourceStart] === backslashN)) {
			sourceStart++;
		}
		this.b.copy(this.b, 0, sourceStart);
		this.a = this.a - sourceStart;
		return result;
	}
}

class Reader<T> extends $e {

	private readonly a: ProtocolBuffer = new ProtocolBuffer();
	private b: number = -1;

	public constructor(readable: Readable) {
		super();
		readable.on('data', data => this.j(data));
	}

	private readonly f = this.g(new vscode.EventEmitter<Error>());
	public readonly onError = this.f.event;

	private readonly i = this.g(new vscode.EventEmitter<T>());
	public readonly onData = this.i.event;

	private j(data: Buffer | string): void {
		if (this.h) {
			return;
		}

		try {
			this.a.append(data);
			while (true) {
				if (this.b === -1) {
					this.b = this.a.tryReadContentLength();
					if (this.b === -1) {
						return;
					}
				}
				const msg = this.a.tryReadContent(this.b);
				if (msg === null) {
					return;
				}
				this.b = -1;
				const json = JSON.parse(msg);
				this.i.fire(json);
			}
		} catch (e) {
			this.f.fire(e);
		}
	}
}

function generatePatchedEnv(env: any, modulePath: string, hasExecPath: boolean): any {
	const newEnv = Object.assign({}, env);

	if (!hasExecPath) {
		newEnv['ELECTRON_RUN_AS_NODE'] = '1';
	}
	newEnv['NODE_PATH'] = path.join(modulePath, '..', '..', '..');

	// Ensure we always have a PATH set
	newEnv['PATH'] = newEnv['PATH'] || process.env.PATH;

	return newEnv;
}

function getExecArgv(kind: TsServerProcessKind, configuration: TypeScriptServiceConfiguration): string[] {
	const args: string[] = [];

	const debugPort = getDebugPort(kind);
	if (debugPort) {
		const inspectFlag = getTssDebugBrk() ? '--inspect-brk' : '--inspect';
		args.push(`${inspectFlag}=${debugPort}`);
	}

	if (configuration.maxTsServerMemory) {
		args.push(`--max-old-space-size=${configuration.maxTsServerMemory}`);
	}

	return args;
}

function getDebugPort(kind: TsServerProcessKind): number | undefined {
	if (kind === TsServerProcessKind.Syntax) {
		// We typically only want to debug the main semantic server
		return undefined;
	}
	const value = getTssDebugBrk() || getTssDebug();
	if (value) {
		const port = parseInt(value);
		if (!isNaN(port)) {
			return port;
		}
	}
	return undefined;
}

function getTssDebug(): string | undefined {
	return process.env[vscode.env.remoteName ? 'TSS_REMOTE_DEBUG' : 'TSS_DEBUG'];
}

function getTssDebugBrk(): string | undefined {
	return process.env[vscode.env.remoteName ? 'TSS_REMOTE_DEBUG_BRK' : 'TSS_DEBUG_BRK'];
}

class IpcChildServerProcess extends $e implements TsServerProcess {
	constructor(
		private readonly a: child_process.ChildProcess,
	) {
		super();
	}

	write(serverRequest: Proto.Request): void {
		this.a.send(serverRequest);
	}

	onData(handler: (data: Proto.Response) => void): void {
		this.a.on('message', handler);
	}

	onExit(handler: (code: number | null, signal: string | null) => void): void {
		this.a.on('exit', handler);
	}

	onError(handler: (err: Error) => void): void {
		this.a.on('error', handler);
	}

	kill(): void {
		this.a.kill();
	}
}

class StdioChildServerProcess extends $e implements TsServerProcess {
	private readonly a: Reader<Proto.Response>;

	constructor(
		private readonly b: child_process.ChildProcess,
	) {
		super();
		this.a = this.g(new Reader<Proto.Response>(this.b.stdout!));
	}

	write(serverRequest: Proto.Request): void {
		this.b.stdin!.write(JSON.stringify(serverRequest) + '\r\n', 'utf8');
	}

	onData(handler: (data: Proto.Response) => void): void {
		this.a.onData(handler);
	}

	onExit(handler: (code: number | null, signal: string | null) => void): void {
		this.b.on('exit', handler);
	}

	onError(handler: (err: Error) => void): void {
		this.b.on('error', handler);
		this.a.onError(handler);
	}

	kill(): void {
		this.b.kill();
		this.a.dispose();
	}
}

export class $wd implements TsServerProcessFactory {
	fork(
		version: $_,
		args: readonly string[],
		kind: TsServerProcessKind,
		configuration: TypeScriptServiceConfiguration,
		versionManager: $db,
		nodeVersionManager: $eb,
		_tsserverLog: TsServerLog | undefined,
	): TsServerProcess {
		let tsServerPath = version.tsServerPath;

		if (!fs.existsSync(tsServerPath)) {
			vscode.window.showWarningMessage(vscode.l10n.t("The path {0} doesn\'t point to a valid tsserver install. Falling back to bundled TypeScript version.", tsServerPath));
			versionManager.reset();
			tsServerPath = versionManager.currentVersion.tsServerPath;
		}

		const execPath = nodeVersionManager.currentVersion;

		const env = generatePatchedEnv(process.env, tsServerPath, !!execPath);
		const runtimeArgs = [...args];
		const execArgv = getExecArgv(kind, configuration);
		const useIpc = !execPath && version.apiVersion?.gte($X.v460);
		if (useIpc) {
			runtimeArgs.push('--useNodeIpc');
		}

		const childProcess = execPath ?
			child_process.spawn(execPath, [...execArgv, tsServerPath, ...runtimeArgs], {
				shell: true,
				windowsHide: true,
				cwd: undefined,
				env,
			}) :
			child_process.fork(tsServerPath, runtimeArgs, {
				silent: true,
				cwd: undefined,
				env,
				execArgv,
				stdio: useIpc ? ['pipe', 'pipe', 'pipe', 'ipc'] : undefined,
			});

		return useIpc ? new IpcChildServerProcess(childProcess) : new StdioChildServerProcess(childProcess);
	}
}
