import * as vscode from 'vscode';
import { DocumentSelector } from '../configuration/documentSelector';
import { LanguageDescription } from '../configuration/languageDescription';
import { TelemetryReporter } from '../logging/telemetry';
import { $X } from '../tsServer/api';
import type * as Proto from '../tsServer/protocol/protocol';
import { Location, Position } from '../typeConverters';
import { ClientCapability, ITypeScriptServiceClient } from '../typescriptService';
import { $e } from '../utils/dispose';
import $wb, { $xb, $yb } from './fileConfigurationManager';
import { $Eb, $Fb, $Hb } from './util/dependentRegistration';


const inlayHintSettingNames = Object.freeze([
	$xb.parameterNamesSuppressWhenArgumentMatchesName,
	$xb.parameterNamesEnabled,
	$xb.variableTypesEnabled,
	$xb.variableTypesSuppressWhenTypeMatchesName,
	$xb.propertyDeclarationTypesEnabled,
	$xb.functionLikeReturnTypesEnabled,
	$xb.enumMemberValuesEnabled,
]);

class TypeScriptInlayHintsProvider extends $e implements vscode.InlayHintsProvider {

	public static readonly minVersion = $X.v440;

	private readonly a = this.g(new vscode.EventEmitter<void>());
	public readonly onDidChangeInlayHints = this.a.event;

	private b = false;

	constructor(
		private readonly f: LanguageDescription,
		private readonly i: ITypeScriptServiceClient,
		private readonly j: $wb,
		private readonly k: TelemetryReporter,
	) {
		super();

		this.g(vscode.workspace.onDidChangeConfiguration(e => {
			if (inlayHintSettingNames.some(settingName => e.affectsConfiguration(f.id + '.' + settingName))) {
				this.a.fire();
			}
		}));

		// When a JS/TS file changes, change inlay hints for all visible editors
		// since changes in one file can effect the hints the others.
		this.g(vscode.workspace.onDidChangeTextDocument(e => {
			if (f.languageIds.includes(e.document.languageId)) {
				this.a.fire();
			}
		}));
	}

	async provideInlayHints(model: vscode.TextDocument, range: vscode.Range, token: vscode.CancellationToken): Promise<vscode.InlayHint[] | undefined> {
		const filepath = this.i.toOpenTsFilePath(model);
		if (!filepath) {
			return;
		}

		if (!areInlayHintsEnabledForFile(this.f, model)) {
			return;
		}

		const start = model.offsetAt(range.start);
		const length = model.offsetAt(range.end) - start;

		await this.j.ensureConfigurationForDocument(model, token);
		if (token.isCancellationRequested) {
			return;
		}

		if (!this.b) {
			this.b = true;
			/* __GDPR__
				"inlayHints.provide" : {
					"owner": "mjbvz",
					"${include}": [
						"${TypeScriptCommonProperties}"
					]
				}
			*/
			this.k.logTelemetry('inlayHints.provide', {});
		}

		const response = await this.i.execute('provideInlayHints', { file: filepath, start, length }, token);
		if (response.type !== 'response' || !response.success || !response.body) {
			return;
		}

		return response.body.map(hint => {
			const result = new vscode.InlayHint(
				Position.fromLocation(hint.position),
				this.l(hint),
				fromProtocolInlayHintKind(hint.kind)
			);
			result.paddingLeft = hint.whitespaceBefore;
			result.paddingRight = hint.whitespaceAfter;
			return result;
		});
	}

	private l(tsHint: Proto.InlayHintItem): string | vscode.InlayHintLabelPart[] {
		if (tsHint.displayParts) {
			return tsHint.displayParts.map((part): vscode.InlayHintLabelPart => {
				const out = new vscode.InlayHintLabelPart(part.text);
				if (part.span) {
					out.location = Location.fromTextSpan(this.i.toResource(part.span.file), part.span);
				}
				return out;
			});
		}

		return tsHint.text;
	}
}

function fromProtocolInlayHintKind(kind: Proto.InlayHintKind): vscode.InlayHintKind | undefined {
	switch (kind) {
		case 'Parameter': return vscode.InlayHintKind.Parameter;
		case 'Type': return vscode.InlayHintKind.Type;
		case 'Enum': return undefined;
		default: return undefined;
	}
}

function areInlayHintsEnabledForFile(language: LanguageDescription, document: vscode.TextDocument) {
	const config = vscode.workspace.getConfiguration(language.id, document);
	const preferences = $yb(config);

	return preferences.includeInlayParameterNameHints === 'literals' ||
		preferences.includeInlayParameterNameHints === 'all' ||
		preferences.includeInlayEnumMemberValueHints ||
		preferences.includeInlayFunctionLikeReturnTypeHints ||
		preferences.includeInlayFunctionParameterTypeHints ||
		preferences.includeInlayPropertyDeclarationTypeHints ||
		preferences.includeInlayVariableTypeHints;
}

export function $Fc(
	selector: DocumentSelector,
	language: LanguageDescription,
	client: ITypeScriptServiceClient,
	fileConfigurationManager: $wb,
	telemetryReporter: TelemetryReporter,
) {
	return $Eb([
		$Fb(client, TypeScriptInlayHintsProvider.minVersion),
		$Hb(client, ClientCapability.Semantic),
	], () => {
		const provider = new TypeScriptInlayHintsProvider(language, client, fileConfigurationManager, telemetryReporter);
		return vscode.languages.registerInlayHintsProvider(selector.semantic, provider);
	});
}
