import VsCodeTelemetryReporter from '@vscode/extension-telemetry';
import * as fs from 'fs';
import * as vscode from 'vscode';
import { Api, $g } from './api';
import { $k } from './commands/commandManager';
import { $fd } from './commands/index';
import { $h, IExperimentationTelemetryReporter } from './experimentTelemetryReporter';
import { $i } from './experimentationService';
import { $kd, $ld } from './lazyClientHost';
import { $ud } from './tsServer/cancellation.electron';
import { $vd } from './tsServer/logDirectoryProvider.electron';
import { $wd } from './tsServer/serverProcess.electron';
import { $xd } from './tsServer/versionProvider.electron';
import { $1c } from './ui/activeJsTsEditorTracker';
import { $yd } from './configuration/configuration.electron';
import { $zd } from './utils/fs.electron';
import { $4 } from './logging/logger';
import { $od } from './utils/packageInfo';
import { $f } from './tsServer/plugins';
import * as temp from './utils/temp.electron';

export function activate(
	context: vscode.ExtensionContext
): Api {
	const pluginManager = new $f();
	context.subscriptions.push(pluginManager);

	const commandManager = new $k();
	context.subscriptions.push(commandManager);

	const onCompletionAccepted = new vscode.EventEmitter<vscode.CompletionItem>();
	context.subscriptions.push(onCompletionAccepted);

	const logDirectoryProvider = new $vd(context);
	const versionProvider = new $xd();

	const activeJsTsEditorTracker = new $1c();
	context.subscriptions.push(activeJsTsEditorTracker);

	let experimentTelemetryReporter: IExperimentationTelemetryReporter | undefined;
	const packageInfo = $od(context);
	if (packageInfo) {
		const { name: id, version, aiKey } = packageInfo;
		const vscTelemetryReporter = new VsCodeTelemetryReporter(aiKey);
		experimentTelemetryReporter = new $h(vscTelemetryReporter);
		context.subscriptions.push(experimentTelemetryReporter);

		// Currently we have no experiments, but creating the service adds the appropriate
		// shared properties to the ExperimentationTelemetryReporter we just created.
		new $i(experimentTelemetryReporter, id, version, context.globalState);
	}

	const logger = new $4();

	const lazyClientHost = $kd(context, $zd(), {
		pluginManager,
		commandManager,
		logDirectoryProvider,
		cancellerFactory: $ud,
		versionProvider,
		processFactory: new $wd(),
		activeJsTsEditorTracker,
		serviceConfigurationProvider: new $yd(),
		experimentTelemetryReporter,
		logger,
	}, item => {
		onCompletionAccepted.fire(item);
	});

	$fd(commandManager, lazyClientHost, pluginManager, activeJsTsEditorTracker);

	import('./task/taskProvider').then(module => {
		context.subscriptions.push(module.$Qd(lazyClientHost.map(x => x.serviceClient)));
	});

	import('./languageFeatures/tsconfig').then(module => {
		context.subscriptions.push(module.$pd());
	});

	context.subscriptions.push($ld(lazyClientHost, pluginManager, activeJsTsEditorTracker));

	return $g(onCompletionAccepted.event, pluginManager);
}

export function deactivate() {
	fs.rmSync(temp.$rd(), { recursive: true, force: true });
}
