import * as vscode from 'vscode';
import { DocumentSelector } from '../../configuration/documentSelector';
import { LanguageDescription } from '../../configuration/languageDescription';
import { $zb } from '../../tsServer/cachedResponse';
import type * as Proto from '../../tsServer/protocol/protocol';
import * as PConst from '../../tsServer/protocol/protocol.const';
import * as typeConverters from '../../typeConverters';
import { ClientCapability, ITypeScriptServiceClient } from '../../typescriptService';
import { $Eb, $Gb, $Hb } from '../util/dependentRegistration';
import { $Kb, $Lb, $Mb } from './baseCodeLensProvider';


export default class $Nb extends $Lb {

	public async resolveCodeLens(
		codeLens: $Kb,
		token: vscode.CancellationToken,
	): Promise<vscode.CodeLens> {
		const args = typeConverters.Position.toFileLocationRequestArgs(codeLens.file, codeLens.range.start);
		const response = await this.a.execute('implementation', args, token, { lowPriority: true, cancelOnResourceChange: codeLens.document });
		if (response.type !== 'response' || !response.body) {
			codeLens.command = response.type === 'cancelled'
				? $Lb.cancelledCommand
				: $Lb.errorCommand;
			return codeLens;
		}

		const locations = response.body
			.map(reference =>
				// Only take first line on implementation: https://github.com/microsoft/vscode/issues/23924
				new vscode.Location(this.a.toResource(reference.file),
					reference.start.line === reference.end.line
						? typeConverters.Range.fromTextSpan(reference)
						: new vscode.Range(
							typeConverters.Position.fromLocation(reference.start),
							new vscode.Position(reference.start.line, 0))))
			// Exclude original from implementations
			.filter(location =>
				!(location.uri.toString() === codeLens.document.toString() &&
					location.range.start.line === codeLens.range.start.line &&
					location.range.start.character === codeLens.range.start.character));

		codeLens.command = this.e(locations, codeLens);
		return codeLens;
	}

	private e(locations: vscode.Location[], codeLens: $Kb): vscode.Command | undefined {
		return {
			title: this.f(locations),
			command: locations.length ? 'editor.action.showReferences' : '',
			arguments: [codeLens.document, codeLens.range.start, locations]
		};
	}

	private f(locations: vscode.Location[]): string {
		return locations.length === 1
			? vscode.l10n.t("1 implementation")
			: vscode.l10n.t("{0} implementations", locations.length);
	}

	protected c(
		document: vscode.TextDocument,
		item: Proto.NavigationTree,
		_parent: Proto.NavigationTree | undefined
	): vscode.Range | undefined {
		switch (item.kind) {
			case PConst.$Y.interface:
				return $Mb(document, item);

			case PConst.$Y.class:
			case PConst.$Y.method:
			case PConst.$Y.memberVariable:
			case PConst.$Y.memberGetAccessor:
			case PConst.$Y.memberSetAccessor:
				if (item.kindModifiers.match(/\babstract\b/g)) {
					return $Mb(document, item);
				}
				break;
		}
		return undefined;
	}
}

export function $Ob(
	selector: DocumentSelector,
	language: LanguageDescription,
	client: ITypeScriptServiceClient,
	cachedResponse: $zb<Proto.NavTreeResponse>,
) {
	return $Eb([
		$Gb(language.id, 'implementationsCodeLens.enabled'),
		$Hb(client, ClientCapability.Semantic),
	], () => {
		return vscode.languages.registerCodeLensProvider(selector.semantic,
			new $Nb(client, cachedResponse));
	});
}
