import * as vscode from 'vscode';
import { DocumentSelector } from '../configuration/documentSelector';
import { $zb } from '../tsServer/cachedResponse';
import { $Cb } from '../tsServer/protocol/modifiers';
import type * as Proto from '../tsServer/protocol/protocol';
import * as PConst from '../tsServer/protocol/protocol.const';
import * as typeConverters from '../typeConverters';
import { ITypeScriptServiceClient } from '../typescriptService';

const getSymbolKind = (kind: string): vscode.SymbolKind => {
	switch (kind) {
		case PConst.$Y.module: return vscode.SymbolKind.Module;
		case PConst.$Y.class: return vscode.SymbolKind.Class;
		case PConst.$Y.enum: return vscode.SymbolKind.Enum;
		case PConst.$Y.interface: return vscode.SymbolKind.Interface;
		case PConst.$Y.method: return vscode.SymbolKind.Method;
		case PConst.$Y.memberVariable: return vscode.SymbolKind.Property;
		case PConst.$Y.memberGetAccessor: return vscode.SymbolKind.Property;
		case PConst.$Y.memberSetAccessor: return vscode.SymbolKind.Property;
		case PConst.$Y.variable: return vscode.SymbolKind.Variable;
		case PConst.$Y.const: return vscode.SymbolKind.Variable;
		case PConst.$Y.localVariable: return vscode.SymbolKind.Variable;
		case PConst.$Y.function: return vscode.SymbolKind.Function;
		case PConst.$Y.localFunction: return vscode.SymbolKind.Function;
		case PConst.$Y.constructSignature: return vscode.SymbolKind.Constructor;
		case PConst.$Y.constructorImplementation: return vscode.SymbolKind.Constructor;
	}
	return vscode.SymbolKind.Variable;
};

class TypeScriptDocumentSymbolProvider implements vscode.DocumentSymbolProvider {

	public constructor(
		private readonly a: ITypeScriptServiceClient,
		private readonly b: $zb<Proto.NavTreeResponse>,
	) { }

	public async provideDocumentSymbols(document: vscode.TextDocument, token: vscode.CancellationToken): Promise<vscode.DocumentSymbol[] | undefined> {
		const file = this.a.toOpenTsFilePath(document);
		if (!file) {
			return undefined;
		}

		const args: Proto.FileRequestArgs = { file };
		const response = await this.b.execute(document, () => this.a.execute('navtree', args, token));
		if (response.type !== 'response' || !response.body?.childItems) {
			return undefined;
		}

		// The root represents the file. Ignore this when showing in the UI
		const result: vscode.DocumentSymbol[] = [];
		for (const item of response.body.childItems) {
			TypeScriptDocumentSymbolProvider.c(document.uri, result, item);
		}
		return result;
	}

	private static c(
		resource: vscode.Uri,
		output: vscode.DocumentSymbol[],
		item: Proto.NavigationTree,
	): boolean {
		let shouldInclude = TypeScriptDocumentSymbolProvider.e(item);
		if (!shouldInclude && !item.childItems?.length) {
			return false;
		}

		const children = new Set(item.childItems || []);
		for (const span of item.spans) {
			const range = typeConverters.Range.fromTextSpan(span);
			const symbolInfo = TypeScriptDocumentSymbolProvider.d(item, range);

			for (const child of children) {
				if (child.spans.some(span => !!range.intersection(typeConverters.Range.fromTextSpan(span)))) {
					const includedChild = TypeScriptDocumentSymbolProvider.c(resource, symbolInfo.children, child);
					shouldInclude = shouldInclude || includedChild;
					children.delete(child);
				}
			}

			if (shouldInclude) {
				output.push(symbolInfo);
			}
		}

		return shouldInclude;
	}

	private static d(item: Proto.NavigationTree, range: vscode.Range): vscode.DocumentSymbol {
		const selectionRange = item.nameSpan ? typeConverters.Range.fromTextSpan(item.nameSpan) : range;
		let label = item.text;

		switch (item.kind) {
			case PConst.$Y.memberGetAccessor: label = `(get) ${label}`; break;
			case PConst.$Y.memberSetAccessor: label = `(set) ${label}`; break;
		}

		const symbolInfo = new vscode.DocumentSymbol(
			label,
			'',
			getSymbolKind(item.kind),
			range,
			range.contains(selectionRange) ? selectionRange : range);


		const kindModifiers = $Cb(item.kindModifiers);
		if (kindModifiers.has(PConst.$1.deprecated)) {
			symbolInfo.tags = [vscode.SymbolTag.Deprecated];
		}

		return symbolInfo;
	}

	private static e(item: Proto.NavigationTree | Proto.NavigationBarItem): boolean {
		if (item.kind === PConst.$Y.alias) {
			return false;
		}
		return !!(item.text && item.text !== '<function>' && item.text !== '<class>');
	}
}

export function $7b(
	selector: DocumentSelector,
	client: ITypeScriptServiceClient,
	cachedResponse: $zb<Proto.NavTreeResponse>,
) {
	return vscode.languages.registerDocumentSymbolProvider(selector.syntax,
		new TypeScriptDocumentSymbolProvider(client, cachedResponse), { label: 'TypeScript' });
}
