import * as vscode from 'vscode';
import { TypeScriptServiceConfiguration } from '../configuration/configuration';
import { $cb } from '../utils/async';
import { $e } from '../utils/dispose';
import { ITypeScriptVersionProvider, $_ } from './versionProvider';


const useWorkspaceTsdkStorageKey = 'typescript.useWorkspaceTsdk';
const suppressPromptWorkspaceTsdkStorageKey = 'typescript.suppressPromptWorkspaceTsdk';

interface QuickPickItem extends vscode.QuickPickItem {
	run(): void;
}

export class $db extends $e {

	private a: $_;

	public constructor(
		private b: TypeScriptServiceConfiguration,
		private readonly e: ITypeScriptVersionProvider,
		private readonly f: vscode.Memento
	) {
		super();

		this.a = this.e.defaultVersion;

		if (this.n) {
			if (vscode.workspace.isTrusted) {
				const localVersion = this.e.localVersion;
				if (localVersion) {
					this.a = localVersion;
				}
			} else {
				this.d.push(vscode.workspace.onDidGrantWorkspaceTrust(() => {
					if (this.e.localVersion) {
						this.m(this.e.localVersion);
					}
				}));
			}
		}

		if (this.p(b)) {
			$cb(() => {
				this.l();
			});
		}

	}

	private readonly i = this.g(new vscode.EventEmitter<void>());
	public readonly onDidPickNewVersion = this.i.event;

	public updateConfiguration(nextConfiguration: TypeScriptServiceConfiguration) {
		const lastConfiguration = this.b;
		this.b = nextConfiguration;

		if (
			!this.p(lastConfiguration)
			&& this.p(nextConfiguration)
		) {
			this.l();
		}
	}

	public get currentVersion(): $_ {
		return this.a;
	}

	public reset(): void {
		this.a = this.e.bundledVersion;
	}

	public async promptUserForVersion(): Promise<void> {
		const selected = await vscode.window.showQuickPick<QuickPickItem>([
			this.j(),
			...this.k(),
			{
				kind: vscode.QuickPickItemKind.Separator,
				label: '',
				run: () => { /* noop */ },
			},
			LearnMorePickItem,
		], {
			placeHolder: vscode.l10n.t("Select the TypeScript version used for JavaScript and TypeScript language features"),
		});

		return selected?.run();
	}

	private j(): QuickPickItem {
		const bundledVersion = this.e.defaultVersion;
		return {
			label: (!this.n || !vscode.workspace.isTrusted
				? '• '
				: '') + vscode.l10n.t("Use VS Code's Version"),
			description: bundledVersion.displayName,
			detail: bundledVersion.pathLabel,
			run: async () => {
				await this.f.update(useWorkspaceTsdkStorageKey, false);
				this.m(bundledVersion);
			},
		};
	}

	private k(): QuickPickItem[] {
		return this.e.localVersions.map(version => {
			return {
				label: (this.n && vscode.workspace.isTrusted && this.currentVersion.eq(version)
					? '• '
					: '') + vscode.l10n.t("Use Workspace Version"),
				description: version.displayName,
				detail: version.pathLabel,
				run: async () => {
					const trusted = await vscode.workspace.requestWorkspaceTrust();
					if (trusted) {
						await this.f.update(useWorkspaceTsdkStorageKey, true);
						const tsConfig = vscode.workspace.getConfiguration('typescript');
						await tsConfig.update('tsdk', version.pathLabel, false);
						this.m(version);
					}
				},
			};
		});
	}

	private async l(): Promise<void> {
		const workspaceVersion = this.e.localVersion;

		if (workspaceVersion === undefined) {
			throw new Error('Could not prompt to use workspace TypeScript version because no workspace version is specified');
		}

		const allowIt = vscode.l10n.t("Allow");
		const dismissPrompt = vscode.l10n.t("Dismiss");
		const suppressPrompt = vscode.l10n.t("Never in this Workspace");

		const result = await vscode.window.showInformationMessage(vscode.l10n.t("This workspace contains a TypeScript version. Would you like to use the workspace TypeScript version for TypeScript and JavaScript language features?"),
			allowIt,
			dismissPrompt,
			suppressPrompt
		);

		if (result === allowIt) {
			await this.f.update(useWorkspaceTsdkStorageKey, true);
			this.m(workspaceVersion);
		} else if (result === suppressPrompt) {
			await this.f.update(suppressPromptWorkspaceTsdkStorageKey, true);
		}
	}

	private m(pickedVersion: $_) {
		const oldVersion = this.currentVersion;
		this.a = pickedVersion;
		if (!oldVersion.eq(pickedVersion)) {
			this.i.fire();
		}
	}

	private get n(): boolean {
		return this.f.get<boolean>(useWorkspaceTsdkStorageKey, false);
	}

	private get o(): boolean {
		return this.f.get<boolean>(suppressPromptWorkspaceTsdkStorageKey, false);
	}

	private p(configuration: TypeScriptServiceConfiguration) {
		return (
			configuration.localTsdk !== null
			&& configuration.enablePromptUseWorkspaceTsdk === true
			&& this.o === false
			&& this.n === false
		);
	}
}

const LearnMorePickItem: QuickPickItem = {
	label: vscode.l10n.t("Learn more about managing TypeScript versions"),
	description: '',
	run: () => {
		vscode.env.openExternal(vscode.Uri.parse('https://go.microsoft.com/fwlink/?linkid=839919'));
	}
};
