export interface Lazy<T> {
	value: T;
	hasValue: boolean;
	map<R>(f: (x: T) => R): Lazy<R>;
}

class LazyValue<T> implements Lazy<T> {
	private a: boolean = false;
	private b?: T;

	constructor(
		private readonly c: () => T
	) { }

	get value(): T {
		if (!this.a) {
			this.a = true;
			this.b = this.c();
		}
		return this.b!;
	}

	get hasValue(): boolean {
		return this.a;
	}

	public map<R>(f: (x: T) => R): Lazy<R> {
		return new LazyValue(() => f(this.value));
	}
}

export function $4c<T>(getValue: () => T): Lazy<T> {
	return new LazyValue<T>(getValue);
}