import * as vscode from 'vscode';
import type * as Proto from '../tsServer/protocol/protocol';
import { ClientCapability, ITypeScriptServiceClient, ServerType } from '../typescriptService';
import { $Eb, $Hb } from './util/dependentRegistration';
import { DocumentSelector } from '../configuration/documentSelector';
import { $Yb } from './util/textRendering';
import * as typeConverters from '../typeConverters';
import $wb from './fileConfigurationManager';



class TypeScriptHoverProvider implements vscode.HoverProvider {

	public constructor(
		private readonly a: ITypeScriptServiceClient,
		private readonly b: $wb,
	) { }

	public async provideHover(
		document: vscode.TextDocument,
		position: vscode.Position,
		token: vscode.CancellationToken
	): Promise<vscode.Hover | undefined> {
		const filepath = this.a.toOpenTsFilePath(document);
		if (!filepath) {
			return undefined;
		}

		const response = await this.a.interruptGetErr(async () => {
			await this.b.ensureConfigurationForDocument(document, token);

			const args = typeConverters.Position.toFileLocationRequestArgs(filepath, position);
			return this.a.execute('quickinfo', args, token);
		});

		if (response.type !== 'response' || !response.body) {
			return undefined;
		}

		return new vscode.Hover(
			this.c(document.uri, response.body, response._serverType),
			typeConverters.Range.fromTextSpan(response.body));
	}

	private c(
		resource: vscode.Uri,
		data: Proto.QuickInfoResponseBody,
		source: ServerType | undefined,
	) {
		const parts: vscode.MarkdownString[] = [];

		if (data.displayString) {
			const displayParts: string[] = [];

			if (source === ServerType.Syntax && this.a.hasCapabilityForResource(resource, ClientCapability.Semantic)) {
				displayParts.push(
					vscode.l10n.t({
						message: "(loading...)",
						comment: ['Prefix displayed for hover entries while the server is still loading']
					}));
			}

			displayParts.push(data.displayString);
			parts.push(new vscode.MarkdownString().appendCodeblock(displayParts.join(' '), 'typescript'));
		}
		const md = $Yb(data.documentation, data.tags, this.a, resource);
		parts.push(md);
		return parts;
	}
}

export function $Dc(
	selector: DocumentSelector,
	client: ITypeScriptServiceClient,
	fileConfigurationManager: $wb,
): vscode.Disposable {
	return $Eb([
		$Hb(client, ClientCapability.EnhancedSyntax, ClientCapability.Semantic),
	], () => {
		return vscode.languages.registerHoverProvider(selector.syntax,
			new TypeScriptHoverProvider(client, fileConfigurationManager));
	});
}
