import * as vscode from 'vscode';
import { DocumentSelector } from '../configuration/documentSelector';
import { $X } from '../tsServer/api';
import type * as Proto from '../tsServer/protocol/protocol';
import * as typeConverters from '../typeConverters';
import { ITypeScriptServiceClient } from '../typescriptService';
import { $Eb, $Fb } from './util/dependentRegistration';

class SmartSelection implements vscode.SelectionRangeProvider {
	public static readonly minVersion = $X.v350;

	public constructor(
		private readonly a: ITypeScriptServiceClient
	) { }

	public async provideSelectionRanges(
		document: vscode.TextDocument,
		positions: vscode.Position[],
		token: vscode.CancellationToken,
	): Promise<vscode.SelectionRange[] | undefined> {
		const file = this.a.toOpenTsFilePath(document);
		if (!file) {
			return undefined;
		}

		const args: Proto.SelectionRangeRequestArgs = {
			file,
			locations: positions.map(typeConverters.Position.toLocation)
		};
		const response = await this.a.execute('selectionRange', args, token);
		if (response.type !== 'response' || !response.body) {
			return undefined;
		}
		return response.body.map(SmartSelection.b);
	}

	private static b(
		selectionRange: Proto.SelectionRange
	): vscode.SelectionRange {
		return new vscode.SelectionRange(
			typeConverters.Range.fromTextSpan(selectionRange.textSpan),
			selectionRange.parent ? SmartSelection.b(selectionRange.parent) : undefined,
		);
	}
}

export function $Tc(
	selector: DocumentSelector,
	client: ITypeScriptServiceClient,
) {
	return $Eb([
		$Fb(client, SmartSelection.minVersion),
	], () => {
		return vscode.languages.registerSelectionRangeProvider(selector.syntax, new SmartSelection(client));
	});
}
