import * as vscode from 'vscode';
import { DocumentSelector } from '../../configuration/documentSelector';
import { LanguageDescription } from '../../configuration/languageDescription';
import { $zb } from '../../tsServer/cachedResponse';
import type * as Proto from '../../tsServer/protocol/protocol';
import * as PConst from '../../tsServer/protocol/protocol.const';
import { ExecutionTarget } from '../../tsServer/server';
import * as typeConverters from '../../typeConverters';
import { ClientCapability, ITypeScriptServiceClient } from '../../typescriptService';
import { $Eb, $Gb, $Hb } from '../util/dependentRegistration';
import { $Kb, $Lb, $Mb } from './baseCodeLensProvider';


export class $Pb extends $Lb {
	public constructor(
		client: ITypeScriptServiceClient,
		protected e: $zb<Proto.NavTreeResponse>,
		private readonly f: LanguageDescription
	) {
		super(client, e);
	}

	public async resolveCodeLens(codeLens: $Kb, token: vscode.CancellationToken): Promise<vscode.CodeLens> {
		const args = typeConverters.Position.toFileLocationRequestArgs(codeLens.file, codeLens.range.start);
		const response = await this.a.execute('references', args, token, {
			lowPriority: true,
			executionTarget: ExecutionTarget.Semantic,
			cancelOnResourceChange: codeLens.document,
		});
		if (response.type !== 'response' || !response.body) {
			codeLens.command = response.type === 'cancelled'
				? $Lb.cancelledCommand
				: $Lb.errorCommand;
			return codeLens;
		}

		const locations = response.body.refs
			.filter(reference => !reference.isDefinition)
			.map(reference =>
				typeConverters.Location.fromTextSpan(this.a.toResource(reference.file), reference));

		codeLens.command = {
			title: this.g(locations),
			command: locations.length ? 'editor.action.showReferences' : '',
			arguments: [codeLens.document, codeLens.range.start, locations]
		};
		return codeLens;
	}

	private g(locations: ReadonlyArray<vscode.Location>): string {
		return locations.length === 1
			? vscode.l10n.t("1 reference")
			: vscode.l10n.t("{0} references", locations.length);
	}

	protected c(
		document: vscode.TextDocument,
		item: Proto.NavigationTree,
		parent: Proto.NavigationTree | undefined
	): vscode.Range | undefined {
		if (parent && parent.kind === PConst.$Y.enum) {
			return $Mb(document, item);
		}

		switch (item.kind) {
			case PConst.$Y.function: {
				const showOnAllFunctions = vscode.workspace.getConfiguration(this.f.id).get<boolean>('referencesCodeLens.showOnAllFunctions');
				if (showOnAllFunctions) {
					return $Mb(document, item);
				}
			}
			// fallthrough

			case PConst.$Y.const:
			case PConst.$Y.let:
			case PConst.$Y.variable:
				// Only show references for exported variables
				if (/\bexport\b/.test(item.kindModifiers)) {
					return $Mb(document, item);
				}
				break;

			case PConst.$Y.class:
				if (item.text === '<class>') {
					break;
				}
				return $Mb(document, item);

			case PConst.$Y.interface:
			case PConst.$Y.type:
			case PConst.$Y.enum:
				return $Mb(document, item);

			case PConst.$Y.method:
			case PConst.$Y.memberGetAccessor:
			case PConst.$Y.memberSetAccessor:
			case PConst.$Y.constructorImplementation:
			case PConst.$Y.memberVariable:
				// Don't show if child and parent have same start
				// For https://github.com/microsoft/vscode/issues/90396
				if (parent &&
					typeConverters.Position.fromLocation(parent.spans[0].start).isEqual(typeConverters.Position.fromLocation(item.spans[0].start))
				) {
					return undefined;
				}

				// Only show if parent is a class type object (not a literal)
				switch (parent?.kind) {
					case PConst.$Y.class:
					case PConst.$Y.interface:
					case PConst.$Y.type:
						return $Mb(document, item);
				}
				break;
		}

		return undefined;
	}
}

export function $Qb(
	selector: DocumentSelector,
	language: LanguageDescription,
	client: ITypeScriptServiceClient,
	cachedResponse: $zb<Proto.NavTreeResponse>,
) {
	return $Eb([
		$Gb(language.id, 'referencesCodeLens.enabled'),
		$Hb(client, ClientCapability.Semantic),
	], () => {
		return vscode.languages.registerCodeLensProvider(selector.semantic,
			new $Pb(client, cachedResponse, language));
	});
}
